<template>
  <div class="login df aic jcc">
    <el-form :model="form" hide-required-asterisk class="plr40 br10 login-form">
      <h3>和助理商城后台</h3>
      <el-form-item>
        <el-input
          v-model="form.user"
          type="text"
          prefix-icon="el-icon-user-solid"
          placeholder="请输入账号"
          @keyup.enter.native="handleLogin"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-input
          v-model="form.password"
          prefix-icon="el-icon-lock"
          placeholder="请输入密码"
          show-password
          @keyup.enter.native="handleLogin"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-checkbox v-model="checked">记住我</el-checkbox>
      </el-form-item>
      <el-form-item>
        <el-button
          class="df aic jcc login-button"
          type="primary"
          @click.native.prevent="handleLogin"
        >
          <span>登录</span>
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      form: {
        user: "",
        password: ""
      },
      checked: false //控制登录的时候是否记住我，只有登录成功了才记住我
    };
  },
  methods: {
    handleLogin() {
      console.log(this)
      this.http({
        url:"user/login",
        data:{
          uid:"10086",
          password:"qwertyuiop"
        }
      }).then(res=>{
        console.log("返回值",res)
      })
     // console.log(this.http)
      // const loading = this.$loading({
      //   lock: true,
      //   text: "拼命加载中...",
      //   spinner: "el-icon-loading",
      //   background: "rgba(0, 0, 0, 0.8)"
      // });
      // setTimeout(() => {
      //   loading.close();
      //   this.$router.replace("/home")
      // }, 2000);
    }
  }
};
</script>

<style>
.login {
  background: url(../../assets/img/login.jpg) no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
}
.login-form {
  background: #fff;
  width: 300px;
}
.login-form > h3 {
  text-align: center;
}
.login-button {
  width: 100%;
}
</style>


